body{font-family:'Open Sans', sans-serif;background-color:#1e1e1e}*{box-sizing:border-box}ul{list-style-type:none;padding:0}button,.button{border:2px solid;padding:13px 30px;background-color:transparent;color:white;font-weight:600;font-size:16px;text-decoration:none}button.button-yellow,.button.button-yellow{border-color:#faaf40}button.button-borderless,.button.button-borderless{border-color:transparent}.CaseStudy{text-align:center;background-color:#1e1e1e;color:white}.CaseStudy__hero{height:600px;display:flex;flex-direction:column;justify-content:center;background-position:top center;background-size:cover}.CaseStudy__hero h1{font-size:40px;font-weight:800;margin-bottom:10px}.CaseStudy__hero h2{font-size:16px;font-weight:700;margin-top:0}.CaseStudy__hero-disney{background-position:top right}.CaseStudy__hero,.CaseStudy__content{position:relative;padding:0 24px}.CaseStudy__back{position:absolute;text-align:left;top:24px;padding:0;border:none;color:white;text-decoration:none;font-weight:700}.CaseStudy__device{position:absolute;display:block;top:0;left:0;transform:translate(10%, -50%);width:90%}.CaseStudy__summary{color:#faaf40;letter-spacing:2;font-size:16px;font-weight:600;padding-top:180px}.CaseStudy__stats{display:flex;flex-wrap:wrap;justify-content:space-between;margin-top:40px}.CaseStudyStat{text-align:left;width:120px;margin-bottom:40px}.CaseStudyStat__label{color:#faaf40;font-weight:600}.CaseStudy__line{width:55px;margin-left:0;border:2px solid #faaf40}.CaseStudy__image{display:block;width:100%;margin:80px 0}.CaseStudySection{position:relative;margin:80px 0}.CaseStudySection__label{position:absolute;background-color:#1e1e1e;padding-bottom:10px;padding-right:20px;top:0;left:0;transform:translateY(-50%)}.CaseStudySection__title{font-size:32px;font-weight:800;text-align:left;margin:0}.CaseStudySection img{width:100%}

